<template>
  <Container
    bordered
    box-shadow
    class="select"
    v-click-outside="hideDropdown"
  >
    <div class="select__body" @click="showDropdown = !showDropdown">
      <slot name="selected" />
      <Icon name="chevron-down" class="select__body__arrow" />
    </div>
    <Container
      bordered
      box-shadow
      v-if="showDropdown"
      class="select__options"
    >
      <div
        v-for="item in filteredOptions"
        :key="item.value"
        class="select__options__item"
        @click="handleChange(item)"
      >
        <slot name="option" :option="item" />
      </div>
    </Container>
  </Container>
</template>

<script>
import { ref, computed } from 'vue'
import Icon from '@/components/Icon/Icon'
import Container from '@/components/Container'
import clickOutside from '@/directives/clickOutside'
export default {
  name: 'Select',
  components: { Container, Icon },
  emits: ['update:modelValue'],
  props: {
    options: {
      type: Array,
      default: () => []
    },
    modelValue: {
      type: Object,
      default: () => ({})
    }
  },
  directives: {
    clickOutside
  },
  setup (props, { emit }) {
    const showDropdown = ref(false)
    const hideDropdown = () => {
      showDropdown.value = false
    }

    const handleChange = (item) => {
      selectedItem.value = item
    }

    const selectedItem = computed({
      get () {
        return props.modelValue
      },
      set (val) {
        emit('update:modelValue', val)
      }
    })

    const filteredOptions = computed(() => {
      return props.options.filter(item => item.value !== props.modelValue.value)
    })

    return {
      showDropdown,
      filteredOptions,
      hideDropdown,
      handleChange
    }
  }
}
</script>

<style lang="scss">
.select {
  position: relative;
  cursor: pointer;

  &__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 9px;

    &__arrow {
      margin-left: 11px;
    }
  }

  &__options {
    position: absolute;
    left: 0;
    bottom: calc(100% + 3px);
    width: 100%;

    &__item {
      display: flex;
      justify-content: center;
      padding: 8px 19px;

      &:nth-of-type(2n - 1) {
        border-bottom: 1px solid $borderColor;
      }
    }
  }
}
</style>
