<template>
  <Select
    v-model="lang"
    :options="selectOptions"
    class="lang"
  >
    <template #selected>
      <div class="lang__selected">
        <Icon :name="lang.label"/>
      </div>
    </template>
    <template v-slot:option="{ option }">
      <Icon :name="option.label" />
    </template>
  </Select>
</template>

<script>
import { useI18n } from 'vue-i18n'
import { ref, watch } from 'vue'
import moment from 'moment'
import Icon from '@/components/Icon/Icon'
import Select from '@/components/UIKit/Select/Select'

export default {
  name: 'Lang',
  components: { Select, Icon },
  setup () {
    const { locale } = useI18n()

    const selectOptions = ref([
      {
        label: 'uk',
        value: 'en'
      },
      {
        label: 'ru',
        value: 'ru'
      }
    ])

    const lang = ref({
      label: 'uk',
      value: 'en'
    })

    watch(lang, nv => {
      moment.locale(nv.value)
      locale.value = nv.value
    })

    return {
      lang,
      selectOptions
    }
  }
}
</script>

<style lang="scss">
.lang {
  margin-left: 14px;

  &__selected {
     width: 20px;
   }
}
</style>
